body {
  font-family: 'Lato';
}
button {
  border: 0;
}
button:active,
button:focus,
.currency:active,
.currency:focus {
  outline: none;
}
ul {
  margin: 0;
  padding: 0;
}
li {
  list-style-type: none;
}
a {
  text-decoration: none;
  color: inherit;
}
img {
  max-width: 100%;
}
.wrapper {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 70px;
}
.icon {
  display: inline-block;
  vertical-align: middle;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
  flex-shrink: 0;
}
.h2 {
  font-size: 30px;
  width: 260px;
  text-align: center;
}
.section_name {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: PlayfairDisplay;
  font-weight: 700;
  font-style: italic;
  line-height: 17.76px;
}
.line {
  height: 2px;
  width: 25%;
  background-color: #eaeaea;
}
.romb {
  min-height: 20px;
  min-width: 20px;
  max-height: 20px;
  max-width: 20px;
  margin: 20px;
  background-color: #d9bf8f;
  transform: rotate(45deg);
}
.category,
.gallery,
.brand,
.advantages {
  margin: 60px 0;
}
.header_top_bar {
  padding: 10px 0;
  background-color: #f2f2f2;
}
.header_top_bar .wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #393939;
}
.header_logo .wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header_login ul {
  display: flex;
  justify-content: flex-end;
}
.header_login li {
  padding-left: 30px;
  border-left: 1px solid #393939;
  font-size: 11px;
  margin-right: 30px;
}
.header_login li:hover,
.header_nav li:hover {
  color: #feab12;
}
.header_login li:first-child {
  border: none;
}
.header_login li:last-child {
  margin-right: 0px;
}
.a_phone {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-size: 11px;
}
.a_phone:hover {
  color: #feab12;
}
.a_phone .icon {
  margin-right: 15px;
}
.header_logo {
  padding: 40px 0;
  position: relative;
}
.header_logo:after {
  z-index: 10;
  content: '';
  position: absolute;
  bottom: calc(0% - 65px);
  left: calc(50% - 200px);
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 65px 200px 0 200px;
  border-color: #fff transparent transparent transparent;
}
.header_nav {
  font-family: PlayfairDisplay;
  font-size: 18px;
  font-style: italic;
}
.header_nav ul {
  display: flex;
  justify-content: flex-start;
}
.header_nav li {
  padding-right: 30px;
}
.logo {
  font-size: 72px;
  font-family: "KrinkesDecorPERSONAL";
}
.logo_social_icon {
  display: flex;
  align-items: center;
}
.logo_social_icon_circle {
  width: 30px;
  height: 30px;
  background: white;
  margin-right: 20px;
  border: 2px solid #d9bf8f;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.logo_social_icon_circle:hover {
  border: 2px solid black;
}
.logo_social_icon_circle:hover .icon_header {
  fill: black;
}
.currency {
  background: black;
  color: white;
  font-size: 12px;
  padding: 10px 30px 10px 15px;
  -webkit-appearance: none;
  margin-left: 40px;
  background: url("../images/arrow_to_down.png");
  background-position: 95% 50%;
}
.icon_header {
  fill: #d9bf8f;
}
.header_slider {
  position: relative;
}
.slider_img img {
  display: block;
  width: 100%;
}
.slider_info {
  position: absolute;
  top: 20%;
  min-width: 18%;
  left: calc((100% - 18%)/2);
  text-align: center;
  color: white;
}
.slider_text {
  padding: 20px;
  border: 1px solid #ffffff;
  outline: 1px solid #ffffff;
  outline-offset: -10px;
}
.black_text {
  color: black;
}
.slider_name1 {
  font-size: 19px;
  margin: 20px 0;
}
.slider_name2 {
  font-size: 59px;
  margin: 25px 0;
}
.slider_name3 {
  font-size: 31px;
}
.shop_now_slider {
  background: #feab12;
  color: white;
  font-size: 24px;
  padding: 20px 30px;
  margin: 50px 0;
  cursor: pointer;
  width: 80%;
}
.before_lines {
  position: absolute;
  width: 20%;
  height: 7px;
  bottom: 56%;
  left: 15%;
  border-bottom: 1px solid #fff;
}
.before_lines:before {
  content: '';
  position: absolute;
  width: 50%;
  height: 1px;
  top: 0%;
  left: 49%;
  border-bottom: 1px solid #fff;
}
.after_lines {
  position: absolute;
  width: 20%;
  height: 7px;
  bottom: 56%;
  left: 65%;
  border-bottom: 1px solid #fff;
}
.after_lines:before {
  content: '';
  position: absolute;
  width: 50%;
  height: 1px;
  top: 0%;
  left: 0%;
  border-bottom: 1px solid #fff;
}
.advantages .wrapper {
  display: flex;
  justify-content: space-between;
  color: white;
}
.advantages_items {
  background: #d9bf8f;
  padding: 20px;
  font-size: 14px;
  width: calc(100%/3 - 80px);
  position: relative;
}
.advantages_items:first-child:after {
  content: '';
  position: absolute;
  top: 0;
  right: -39px;
  width: 0;
  height: 0;
  border-style: solid;
  border-top-width: 45px;
  border-right-width: 0;
  border-left-width: 40px;
  border-bottom-width: 45px;
  border-color: transparent transparent transparent #d9bf8f;
  z-index: 5;
}
.advantages_items:last-child:before {
  content: '';
  position: absolute;
  top: 0;
  left: -39px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 45px 40px 45px 0;
  border-color: transparent #d9bf8f transparent transparent;
}
.advantages_centre:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 45px 0 45px 40px;
  border-color: transparent transparent transparent #ffffff;
}
.advantages_centre:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 45px 40px 45px 0;
  border-color: transparent #ffffff transparent transparent;
}
.advantages_name {
  font-size: 30px;
}
.advantages_centre {
  background: black;
  padding: 20px 50px;
}
.category {
  display: flex;
  justify-content: space-between;
}
.category_column {
  width: calc(100%/3 - 20px);
  display: flex;
  flex-flow: column;
  justify-content: space-between;
}
.category_column img {
  display: block;
  width: 100%;
  height: fit-content;
  object-fit: cover;
}
.category_item {
  position: relative;
}
.shop_now {
  display: none;
  padding: 15px;
  font-size: 14px;
  color: white;
  background: #feab12;
  cursor: pointer;
  position: absolute;
  bottom: 60px;
  min-width: 40%;
  left: 30%;
}
.shop_now:hover,
.shop_now_slider:hover {
  color: #feab12;
  background: #000000;
}
.hover_item {
  position: absolute;
  width: calc(100% - 40px);
  height: calc(100% - 40px);
  bottom: 20px;
  left: 20px;
  border: 3px solid #ffffff;
  box-sizing: border-box;
  transition: 0.5s;
}
.cross {
  background: #feab12;
  height: 82px;
  width: 2px;
  position: relative;
  left: 50%;
  bottom: -30%;
  display: none;
}
.cross:after {
  content: "";
  height: 2px;
  width: 82px;
  background: #feab12;
  position: absolute;
  left: -40px;
  top: 40px;
}
.hover_item:hover {
  background: #656469;
  border: 2px solid transparent;
  background-color: rgba(0, 0, 0, 0.3);
}
.hover_item:hover .shop_now {
  display: block;
}
.hover_item:hover .cross {
  display: block;
}
.gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  border: 1px solid #eaeaea;
  padding: 15px;
}
.gallery_item {
  display: block;
  width: calc(100%/4 - 30px);
  margin: 15px;
  position: relative;
}
.gallery_item a {
  display: block;
}
.gallery_item img {
  width: 100%;
  height: 350px;
  object-fit: cover;
}
.circle_sale20 {
  content: "-20%";
}
.circle_sale40 {
  content: "-40%";
}
.circle_sale20,
.circle_sale40 {
  font-size: 23px;
  background: #feab12;
  color: #ffffff;
  border-radius: 50%;
  padding: 5px;
  margin: 10px;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
}
.item_filter:hover {
  filter: brightness(70%);
}
.add_to_cart {
  color: white;
  outline: none;
  border: none;
  cursor: grab;
  position: absolute;
  bottom: 4px;
  min-width: 80%;
  left: 10%;
  background: linear-gradient(135deg, transparent 33px, #000000 0) top left, linear-gradient(-135deg, transparent 33px, #000000 0) top right;
  background-size: 51% 100%;
  background-repeat: no-repeat;
  padding: 15px 33px;
}
.add_to_cart:hover {
  background: linear-gradient(135deg, transparent 33px, #feab12 0) top left, linear-gradient(-135deg, transparent 33px, #feab12 0) top right;
  background-size: 51% 100%;
  background-repeat: no-repeat;
  padding: 15px 33px;
}
.brand {
  display: flex;
  align-items: center;
  justify-content: center;
}
.brand a {
  margin: 0 35px;
}
.designer .wrapper {
  display: flex;
  justify-content: space-between;
  margin-top: 70px;
}
.designer {
  background-color: #d9bf8f;
}
.designer_info {
  display: flex;
  padding: 30px 0 50px 0;
}
.designer_about {
  display: flex;
  flex-flow: column;
  justify-content: flex-end;
}
.designer_about_onright {
  text-align: end;
}
.designer_about p {
  color: #ffffff;
  font-family: PlayfairDisplay;
  font-size: 18px;
  font-style: italic;
  line-height: 30.16px;
  margin: 50px 0;
}
.designer_name {
  color: #000000;
  margin: 20px 0;
  font-family: PlayfairDisplay;
  font-size: 30px;
  font-weight: 700;
  font-style: italic;
  line-height: 17.99px;
}
.designer_work {
  color: #7f7e7e;
  font-size: 14px;
  font-weight: 700;
  line-height: 29.98px;
  text-transform: uppercase;
}
footer {
  background-color: black;
  color: #737373;
}
.footer_big {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  border-bottom: 1px solid #737373;
  padding: 65px 0 80px 0;
  position: relative;
}
.footer_big:before {
  content: '';
  position: absolute;
  bottom: -11px;
  left: calc(50% - 13px);
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 10px 13px 0 13px;
  border-color: #737373 transparent transparent transparent;
}
.footer_h {
  color: #ffffff;
  font-size: 16px;
  font-weight: 700;
  margin: 50px 0;
}
.footer_h span {
  font-style: italic;
}
.footer_info {
  margin-right: 115px;
}
.footer_info li {
  margin: 15px 0;
}
.footer-small {
  padding: 25px 0;
}
.footer-small .icon {
  padding: 25px 25px 25px 0;
  width: 2em;
  height: 2em;
}
.footer_social_icon {
  display: flex;
}
.social_icon_circle {
  width: 50px;
  height: 50px;
  background: white;
  margin-right: 20px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.social_icon_circle:hover {
  background: black;
}
.social_icon_circle:hover .icon_footer {
  fill: white;
}
.icon_footer {
  fill: black;
  font-size: 25px;
}
.search input:focus::placeholder {
  opacity: 0;
  transition: opacity 0.3s ease;
}
.search {
  border: 0.5px solid #c3c3c3;
  background: black;
  outline: none;
  padding: 5px;
  display: flex;
  justify-content: space-between;
  font-size: 12px;
}
.search input {
  background: black;
  border: none;
  outline: none;
  color: #737373;
  padding: 0 20px;
  flex-basis: 70%;
}
.search button {
  background: #323030;
  color: white;
  padding: 10px 20px;
}
.search button:hover {
  cursor: pointer;
  background: white;
  color: black;
}
@font-face {
  font-family: 'Lato';
  src: url('../fonts/LatoLatin-Regular.woff2') format('woff2'), url('../fonts/LatoLatin-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'PlayfairDisplay';
  src: url('../fonts/PlayfairDisplay-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'KrinkesDecorPERSONAL';
  src: url('../fonts/KrinkesDecorPERSONAL.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}
